import { Injectable } from '@angular/core';
import { Associate } from '../models/associate';
import { HttpClient } from '@angular/common/http';

@Injectable()
export class AssociateService {
    private urlServer = 'http://localhost:8080/associate';

    constructor(private http: HttpClient) { }

    public get(id?: number) {
        let url = this.urlServer;
        if (id) {
            url = url + '/' + id;
        }
        return this.http.get<Associate[]>(url);
    }

    public post(body: Associate) {
        return this.http.post(this.urlServer, body);
    }

    public put(body: Associate) {
        return this.http.put(this.urlServer, body);
    }

    public delete(id: number) {
        let url = this.urlServer;
        if (id) {
            url += '/' + id;
        }
        return this.http.delete(url);
    }
}
