export class LoadService {

    templ = `<div id="loader">
    <div class="loader-body">
        <div class="loader-body-header">
            <span class="text">Carregando, Aguarde</span>
        </div>
        <center>
            <div class="loader-circle" id="loading-indicator"></div>
        </center>
    </div>
</div>`;

    private _createElement() {
        try {
            if ($('body').children('div#loader').length === 0) {
                $('div#loader').remove();
                $('body').append($(this.templ));
            }
        } catch (error) {
            console.log('Ocorreu um erro ao criar o elemento de loader');
            console.error(error);
        }
    }

    private _destroyElement() {
        try {
            $('#loader').remove();
        } catch (error) {
            console.log('Ocorreu um erro ao remover o elemento de loader');
            console.error(error);
        }
    }

    public InitLoad(message: string) {
        try {
            this._createElement();
            $('#loader>.loader-body>.loader-body-header>.text').html(message);
        } catch (error) {
            console.log('Ocorreu um erro ao preencher os dados da mensagem "' + message + '"');
            console.error(error);
        }
    }

    public EndLoad(): void {
        this._destroyElement();
    }
}
