import { Component, OnInit, AfterViewInit, OnDestroy, ElementRef } from '@angular/core';

import { Associate } from '../models/associate';
import { AssociateService } from '../services/associate.service';
import { LoadService } from '../services/load.service';

@Component({
    moduleId: module.id,
    templateUrl: 'associate.component.html'
})

export class AssociateComponent extends LoadService implements OnInit, OnDestroy, AfterViewInit {

    banks = {
        '001': '001 - Banco do Brasil S.A.',
        '341': '341 - Banco Itaú S.A.',
        '033': '033 - Banco Santander (Brasil) S.A.',
        '356': '356 - Banco Real S.A. (antigo)',
        '652': '652 - Itaú Unibanco Holding S.A.',
        '237': '237 - Banco Bradesco S.A.',
        '745': '745 - Banco Citibank S.A.',
        '399': '399 - HSBC Bank Brasil S.A. – Banco Múltiplo',
        '104': '104 - Caixa Econômica Federal',
        '389': '389 - Banco Mercantil do Brasil S.A.',
        '453': '453 - Banco Rural S.A.',
        '422': '422 - Banco Safra S.A.',
        '633': '633 - Banco Rendimento S.A.'
    };

    associates: Associate[] = [];
    associate: Associate = new Associate();
    private _el: any;
    private modalElement: JQuery;
    modalTitle = '';
    modalType = 'INSERT';

    constructor(el: ElementRef, private associateService: AssociateService) {
        super();
        this._el = el.nativeElement;
    }

    loadAssociates() {
        this.InitLoad('Carregando os associados');
        this.associateService.get()
            .subscribe(associates => {
                this.associates = associates;
                this.EndLoad();
            }, (error) => this.EndLoad());
    }

    ngOnInit() {
        $('body').append($(this._el).children('#move-to-end'));
        this.loadAssociates();
    }

    ngAfterViewInit() {
        this.modalElement = $('div#associate-modal');
    }

    ngOnDestroy() {
        $(this._el).append($('body').children('#move-to-end'));
    }

    newAssociate() {
        this.associate = new Associate();
        this.modalTitle = `Novo associado`;
        this.modalType = 'INSERT';
        (<any>this.modalElement).modal('show');
    }

    editAssociate(associate: Associate) {
        this.associate = associate;
        this.modalTitle = `Editando ${associate.name}`;
        this.modalType = 'UPDATE';
        (<any>this.modalElement).modal('show');
    }

    removeAssociate(associate: Associate) {
        let swal: any;
        swal({
            title: 'Excluir!!',
            text: `Deseja mesmo exluir ${associate.name}?`,
            icon: 'warning',
            buttons: ['Não', 'Excluir'],
            closeOnClickOutside: false,
            closeOnEsc: false
        }).then(hasConfirmed => {
            if (hasConfirmed) {
                this.InitLoad(`Excluindo ${associate.name}`);
                this.associateService.delete(associate.id)
                    .subscribe(() => {
                        this.EndLoad();
                        let swal: any;
                        swal({title: 'Sucesso', text: `${associate.name} excluido com sucesso`, icon: 'info'});
                        this.loadAssociates();
                    }, (error) => {
                        this.EndLoad();
                        console.log(error);
                    });
            }
        });
    }

    bankChanged(bankCode: string) {
        this.associate.bankCode = Number(bankCode);
        this.associate.bankName = this.banks[bankCode];
    }

    insert(associate: Associate) {
        this.InitLoad(`Inserindo o associado ${associate.name}...`);
        this.associateService.post(associate)
            .subscribe((ret) => {
                this.EndLoad();
                console.log(ret);
                this.loadAssociates();
                let swal: any;
                if ((<any>ret).status === 'SUCCESS') {
                    swal({title: 'Sucesso', text: `${associate.name} cadastrado com sucesso`, icon: 'success'});
                } else if ((<any>ret).status === 'INVALID') {
                    swal({title: 'Inválido', text: (<any>ret).message, icon: 'error'});
                } else {
                    swal({title: 'Erro no servidor', text: (<any>ret).message, icon: 'error'});
                }
            }, (error) => {
                this.EndLoad();
                console.log(error);
            });
    }

    update(associate: Associate) {
        this.InitLoad(`Alterando o associado ${associate.name}...`);
        this.associateService.put(associate)
        .subscribe((ret) => {
            this.EndLoad();
                console.log(ret);
                this.loadAssociates();
                let swal: any;
                if ((<any>ret).status === 'SUCCESS') {
                    swal({title: 'Sucesso', text: `${associate.name} alterado com sucesso`, icon: 'success'});
                } else if ((<any>ret).status === 'INVALID') {
                    swal({title: 'Inválido', text: (<any>ret).message, icon: 'error'});
                } else {
                    swal({title: 'Erro no servidor', text: (<any>ret).message, icon: 'error'});
                }
        }, (error) => {
            this.EndLoad();
            console.log(error);
        });
    }

    submitForm() {
        if (this.associate.id && this.associate.id > 0) {
            this.update(this.associate);
        } else {
            this.insert(this.associate);
        }
    }

    confirmClose() {
        let swal: any;
        swal({
            title: 'Confirmar fechamento',
            text: `Se a tela for fechada as alterações serão perdidas... Gostaria de prosseguir? `,
            icon: 'warning',
            buttons: ['Não', 'Sim, fechar'],
            closeOnClickOutside: false,
            closeOnEsc: false
        }).then((hasConfirmed) => {
            if (hasConfirmed) {
                (<any>this.modalElement).modal('hide');
                this.associate = new Associate();
            }
        });
    }
}
